.navbar{
    width: 100%;
    background-color: #373737;
    color: #fff;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem 0 2rem;
  }

  .left *{
    margin: 0 1em 0 0em;
  }

  .titleStyle {
    font-weight: 700;
  }

  .left a {
    color: #fff;
  }

  .left a:hover {
    color: #d6d6d6;
    text-decoration: none;
  }